import React from 'react';

function SectionExperience() {
    return(
        <section className="resume-section spad">
            <div className="container-fluid">
                <div className="row">
                    <div className="col-xl-7 offset-xl-2">
                        <div className="section-title">
                            <h2>Industry Experience</h2>
                        </div>
                        <ul className="resume-list">
                            <li>
                                <h2>Present</h2>
                                <h3>Post Graduation</h3>
                                <p>I've recently submitted my final year project and expecting a first class degree. I'm currently looking for new opportunities and challenges while working on my own projects.</p>
                            </li>
                            <li>
                                <h2>2018-2019</h2>
                                <h3>The Walton Centre NHS Foundation Trust</h3>
                                <h4>Full Stack Developer</h4>
                                <p>This was my first position as a developer, I gained firsthand experience developing full stack with HTML, CSS/Bootstrap, JQuery, JavaScript, SQL and PHP. I was responsible for developing full stack MVC's for general day to day use in a bespoke in-house system, as well as developing my own systems. Including using Atlassian's JIRA REST API.</p>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </section>
    );
}
function SectionEducation() {
    return(
        <section className="resume-section spad">
            <div className="container-fluid" style={{marginRight:"150px", fontSize:"36px", color:"#40424a"}}>
                <div className="row">
                    <div className="col-xl-7 offset-xl-2">
                        <div className="section-title">
                            <h2>Education</h2>
                        </div>
                        <ul className="resume-list">
                            <li>
                                <h2>2020</h2>
                                <h3>BSc(Hons) Computing</h3>
                                <h4>Edge Hill University</h4>
                                <p>During my time at Edge Hill University I worked with several technologies and languages, including Java, Unity, XML, mySQL, MongoDB, Python, Hadoop and Web Technologies. I also studied Mandarin up to HSK Level 2.</p>
                            </li>
                            <li>
                                <h2>2016</h2>
                                <h3>A Levels</h3>
                                <h4>Maricourt Sixth Form</h4>
                                <p>At Maricourt Sixth Form I gained A levels in Physics, Computing and ICT</p>
                            </li>
                            <li>
                                <h2>2014</h2>
                                <h3>GCSEs</h3>
                                <h4>Maricourt High School</h4>
                                <p>Grade A*: Mathematics</p>
								<p>Grade A: English Literature, Computing, Physics, Biology, Geography, Religious Studies, Core Science</p>
								<p>Grade B: English Language, Chemistry, Philosophy</p>
								<p>Grade C: French</p>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </section>
    );
}

export {SectionExperience, SectionEducation};
